@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

:root {
  --text: white;
  --bg: black;
  --accent: #ce42ff;
  
}

body {
  line-height: auto;
  font-family: 'Poppins', sans-serif;
  background-color: var(--bg);
  height: 100%;
  min-height: 100%;
  transition: .3s;
}

ul {
  list-style-type: none;
}

h1 {
  font-size: 8.333vw;
  color: var(--text);
  font-weight: 900;
  line-height: 106.5%;
  overflow: hidden;
}

h2 {
  font-size: 6.667vw  ;
  color: var(--text);
  font-weight: bold;
  line-height: 106.5%;
  overflow: hidden;

}

h3 {
  font-size: 3.333vw;
  color: var(--text);
  font-weight: 900;
  line-height: 3.889vw;
  overflow: hidden;
}

h4 {
  font-size: 1.667vw;
  color: var(--text);
  font-weight: 900;
  line-height: 3.889vw;
  letter-spacing: .0938rem;
  overflow: hidden;
}

h5 {
  font-size: 0.833vw;
  color: var(--text);
  font-weight: bold;
  overflow: hidden;
}

h6 {
  font-size: 1.111vw;
  font-weight: bold;
  overflow: hidden;
}

p {
  font-size: 1.250vw;
  color: var(--text);
  font-weight: normal;
  line-height: 2.500vw;
  overflow: hidden;
}

/* Title */
.Title {
  position: relative;
  height: 120vh;
  width: 100%;
}

.h4pos {
  position: absolute;
  bottom: 10%;
  z-index: 1;
  padding-left:2.5%;
}

.Title h4:before {
  content: "";
  display: block;
  height: 2px;
  width: 7vw;
  background: var(--accent);
  position: absolute;
  transform: translate(-110%);
  top: 7.5%;
}

.page {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100%;
}

/* Meet the Team */
.mttgrid {
  display: grid;
  grid-template-columns: 40% auto;
  grid-auto-rows: minmax(100px, auto);
  grid-template-areas: "sidebar main";
  padding-top: 100px;
  padding-bottom: 100px;
}

.profile .mttgrid {
  grid-template-columns: 25% auto;
}


.sidebar {
  grid-area: sidebar;
}

.main {
  grid-area: main;
}

.personwrapper {
  display: inline-flex;
  width: 27vw;
  height: 32vw;
  flex-direction: column;
}

.mttgrid img {
  height: 25vw;
  width: 25vw;
  filter: grayscale(100%);
}

.profile .mttgrid img {
  height: 22vw;
  width: 22vw;
  filter: grayscale(100%);
}

/* Profile Page*/
.profilepage {
  width: 100%;
  position: relative;
  margin-top: 150px;
  margin-bottom: 200px;
  overflow: hidden;
}



.greybar2 {
  height: 60vh;
  width: 100%;
  background-color: #c4c4c4;
  position: relative;
}

.greybar2 .h4mid {
  top: 40%;
  text-align: center;
}

.letschat {
  height: 85vh;
  width: 100%;
  position: relative;
}

.letschat .h4pos {
  position: absolute;
  top: 10%;
  left: 2%;
}

.display {
  opacity: 0;
}

.hidden {
  opacity: 100;

}

/* Work Page */
.witsection {
  background-color: black;
  position: relative;
  height: 100%;
  color: white;
}

.wit {
  padding-top: 100px;
  padding-bottom: 100px;
}

.wit img {
  display: block;
  width: 100%;
  height: auto;
  padding-bottom: 0.5vw;
}

.wit .mttgrid {
  padding-top: 3.5vw;
}

.grid {
  margin-top: 150px;
  margin-bottom: 200px;
  display: flex;
  /*grid-template-rows: 50%;*/
  gap: 200px;
  height: 100%;
  padding: 0 40px;
  overflow: visible;
}

.hometext {
  height: 100%;
  background-color: #0000;
  position: relative;
  color: var(--text);
  padding-top: 100px;
}

/* Utilities */
.containerbig {
  position: relative;
  max-width: 90rem;
  padding: 8px;
  margin: 0 auto;
  overflow: visible;
}

.containerrel {
  position: relative;
  max-width: 95%;
  padding: 8px;
  margin: 0 auto;
  overflow: visible;
}

.containersmall {
  position: relative;
  max-width: 80.56%;
  margin: 0 auto;
  overflow: auto;
}

.containersmaller {
  position: relative;
  max-width: 65%;
  margin: 0 auto;
  overflow: auto;
}

.containerform {
  position: relative;
  max-width: 75%;
  margin: 0 auto;
  overflow: auto;
}

.Makuri {
  height: 100%;
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  color: white;
}

.Makuri img {
  position: absolute;
  right: 0;
  top: -140px;
  z-index: -1;
}

.Makuri .mttgrid {
  padding-top: 0;
}

/* Highlight */
.highlight {
  width: max-content;
  background-size: 200% auto;
  animation-duration: 1s;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;

  
}

.highlightani {
  animation-name: highlightani;
}

@keyframes highlightani {
  from {
    background-position: 100% 0;
  }
  to {
    background-position: 0% 0;
    
  }
}

.lblue {
  background-image: linear-gradient(to left, #0000 50%, #4cb3df 50% );
  
}

.green {
  background-image: linear-gradient(to left,#0000 50%,#9FC553 50%);

}

.dblue {
  background-image: linear-gradient(to left,#0000 50%,#3A619E 50%);
}

.red {
  background-image: linear-gradient(to left, #0000 50%,#EC4139 50%);

}

.pink {
  background-image: linear-gradient(to left, #0000 50%,#ce42ff 50%);
}

.last {
  border-bottom: 1px solid grey;
}

/*home page*/



.profilename {
  border-top: 1px solid grey;
  background-color: #0000;
  color: white;
  text-align: left;
  margin: auto;
  padding-top: 35px;
  padding-bottom: 35px;
  width: 99%;
}

.auxprofilegrid {
  margin-top: 50px;
  margin-bottom: 100px;
  display: grid;
  grid-template-columns: 50% auto;
  grid-template-areas: "first second";
}

.methoddiv{
  padding-bottom: 100px
}

.wave {
  animation-name: wave-animation;  /* Refers to the name of your @keyframes element below */
  animation-duration: 4s;        /* Change to speed up or slow down */
  animation-iteration-count: infinite;  /* Never stop waving :) */
  transform-origin: 70% 70%;       /* Pivot around the bottom-left palm */
  display: inline-block;
}

@keyframes wave-animation {
    0% { transform: rotate( 0.0deg) }
   10% { transform: rotate(14.0deg) }  /* The following five values can be played with to make the waving more or less extreme */
   20% { transform: rotate(-8.0deg) }
   30% { transform: rotate(14.0deg) }
   40% { transform: rotate(-4.0deg) }
   50% { transform: rotate(10.0deg) }
   60% { transform: rotate( 0.0deg) }  /* Reset for the last half to pause */
  100% { transform: rotate( 0.0deg) }
}
